





























import Vue from 'vue';
import Component from 'vue-class-component';
import { Prop } from 'vue-property-decorator';
import Swiper from 'swiper';

@Component
export default class Swipe extends Vue {
    @Prop({ required: true })
    lists: any;

    mounted() {
        new Swiper('.swiper-container', {
            loop: true,
            pagination: {
                el: '.swiper-pagination'
            },
            autoplay: true
        });
    }
}



